import React from "react";
import { Container, Row, Col, Spinner } from "react-bootstrap";

const minner = (a, b) => ({ temperature: (a.temperature < b.temperature) ? a.temperature : b.temperature });
const maxxer = (a, b) => ({ temperature: (a.temperature > b.temperature) ? a.temperature : b.temperature });
const dateFilter = (x) => new Date(x.startTime).getDate() === new Date().getDate();

export default class Today extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoaded: false,
      now: {},
      hours: []
    }
  }

  componentDidMount() {
    const url = this.props.loc.hfurl;
    console.log(url);
    fetch(url)
      .then(res => res.json())
      .then(
        (result) => {
          let periods = result.properties.periods;
          let td = periods.filter(dateFilter);
          this.setState({
            now: {
              data: periods[0],
              tmin: td.reduce(minner).temperature,
              tmax: td.reduce(maxxer).temperature
            },
            hours: periods.slice(0,24),
            isLoaded: true
          });
        }
      )
  }

  render() {
    const isLoaded = this.state.isLoaded;
    let content;
    if (!isLoaded) {
      content = <Spinner animation="border" role="status"></Spinner>
    } else {
      content = <Container>
        <Row className="mt-2">
          <Col className="text-center today-box">
            <img className="today-icon"
              src={this.state.now.data.icon}
              alt={this.state.now.data.shortForecast} />
            <span className="today-temp">
              &nbsp;{this.state.now.data.temperature}&deg;
            </span>
          </Col>
        </Row>
        <Row className="mt-2 mb-2 text-center">
          <Col xs={12} sm={4}>
            <small>
              H: {this.state.now.tmax}&deg;
              &nbsp;
              L: {this.state.now.tmin}&deg;
            </small>
          </Col>
          <Col xs={12} sm={4}>
            {this.state.now.data.shortForecast}
          </Col>
          <Col xs={12} sm={4}>
            <small className="text-muted">
              Wind&nbsp;
              {this.state.now.data.windSpeed} {this.state.now.data.windDirection}
            </small>
          </Col>
        </Row>
      </Container>
    }

    return (
      <Container fluid>
        {content}
        <Row className="mt-4">
          <Col className="mt-4">Next 24 Hour Forecast</Col>
        </Row>
        <hr />
        <Row className="mt-2">
          {this.state.hours.map((hour, index) => (
            <Col xs={3} sm={2} lg={1} key={index}>
              <Row className="mt-4">
                <small className="text-muted">
                  {new Date(hour.startTime).toLocaleString('en-US', { hour: 'numeric', hour12: true })}
                </small>
              </Row>
              <Row>
                <span>
                  <strong>{hour.temperature}&deg;</strong>
                </span>
              </Row>
            </Col>
          ))}
        </Row>
      </Container>
    )
  }
}
