import React from "react";
import { Container, Row, Col, Spinner } from "react-bootstrap";

export default class Today extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoaded: false,
      slots: []
    }
  }

  componentDidMount() {
    const url = this.props.loc.dfurl;
    console.log(url);
    fetch(url)
      .then(res => res.json())
      .then(
        (result) => {
          let periods = result.properties.periods;
          this.setState({ 
            slots: periods.slice(0,14),
            isLoaded: true
          });
        }
      )
  }

  render() {
    const isLoaded = this.state.isLoaded;
    let content;
    if (!isLoaded) {
      content = <Spinner animation="border" role="status"></Spinner>
    }

    return (
      <Container className="pl-1 pr-1">
        {content}
        {this.state.slots.map((slot, index) => (
          <Row key={index} className="pt-2 pb-2 row-striped">
            <Col xs={4} sm={3} md={2}>
              <img src={slot.icon} alt={slot.shortForecast} height="60px" />
            </Col>
            <Col>
              <strong>
                {new Date(slot.startTime).toLocaleString('en-US', { day: 'numeric', month: 'numeric' })}
              </strong>
              <br />
              <small>{slot.name}</small>
            </Col>
            <Col xs={12} sm={6}>
              <Row>
                <Col xs={4}>
                  {slot.temperature}&deg;{slot.temperatureUnit}
                </Col>
                <Col>
                  <small>{slot.windSpeed} {slot.windDirection}</small>
                </Col>
              </Row>
              <Row>
                <small>
                  <small>
                    {/* {slot.shortForecast} <br />*/}
                    {slot.detailedForecast}
                  </small>
                </small>
              </Row>
            </Col>
          </Row>
        ))}
      </Container>
    )
  }
}
