import './App.css';
import  React  from "react";
import { Container } from "react-bootstrap";

import Locations from "./components/locations";
import Forecast from "./components/forecast";

import "bootstrap/dist/css/bootstrap.min.css";

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.handleSetLoc = this.handleSetLoc.bind(this);
    this.handleSetTab = this.handleSetTab.bind(this);
    this.state = {
      tab: 'home',
      loc: {}
    }
  }

  handleSetLoc = newLoc => {
    this.setState({loc: newLoc});
  }

  handleSetTab = newTab => {
    this.setState({tab: newTab});
  }

  render() {
    return (
      <Container fluid>
        {this.state.tab === 'forecast' && (
          <Forecast loc={this.state.loc} 
            onTabChange={this.handleSetTab} />
        )}
  
        {this.state.tab !== 'forecast' && (
          <Locations 
            onLocChange={this.handleSetLoc}
            onTabChange={this.handleSetTab} />
        )}
      </Container>
    )
  }
}
