import React from "react";
import { Tabs, Tab, Button, Container, Row, Col } from "react-bootstrap";

// weather-app components
import Today from "./today"
import Weekly from "./weekly"

export default class Forecast extends React.Component {
  constructor(props) {
    super(props);
    this.showLocations = this.showLocations.bind(this);
    this.state = {
      key: "today",
      loc: this.props.loc
    };
  }

  showLocations = () => {
    this.props.onTabChange("home");
  }

  render() {
    return (
      <Container>
        <Row className="mt-3 mb-3">
          <Col>
            <h1>{this.state.loc.name}</h1>
          </Col>
          <Col xs={2} className="text-right">
            <Button variant="secondary"
              onClick={this.showLocations}>
              &lt;
            </Button>
          </Col>
        </Row>
        <Row>
          <Tabs id="weather-forecast-tabs"
            activeKey={this.state.key}
            mountOnEnter={true}
            onSelect={key => this.setState({ key })}
          >
            <Tab eventKey="today" title="Today's Weather">
                <Today loc={this.state.loc}></Today>
            </Tab>
            <Tab eventKey="weekly" title="Next 7 Days">
                <Weekly loc={this.state.loc}></Weekly>
            </Tab>
          </Tabs>
        </Row>
      </Container>
    )
  }
}
