import React from "react";
import { Container, Row, Col, Button, InputGroup, FormControl } from "react-bootstrap";

// move to config later
const GEOCODE_URL = "https://nominatim.openstreetmap.org/search?limit=1&format=json&addressdetails=1&country=US";
const NOAAPOINT_URL = "https://api.weather.gov/points/";

export default class Favorites extends React.Component {

  constructor(props) {
    super(props);
    this.setSearchInput = this.setSearchInput.bind(this);
    this.deleteLocation = this.deleteLocation.bind(this);
    this.showForecast = this.showForecast.bind(this);

    let loc = JSON.parse(localStorage.getItem('locations')) || [];
    this.state = {
      searchInput: "",
      locations: loc,
      editMode: (loc.length > 0) ? false : true
    }
  }

  toggleEditMode = () => {
    this.setState(prevState => ({
      editMode: !prevState.editMode
    }));
  }

  setSearchInput = event => {
    this.setState({ searchInput: event.target.value });
  }

  addLocation = () => {
    let url = GEOCODE_URL;
    let si = this.state.searchInput;

    if (si.length === 5 && parseInt(si) > 0) {
      // use postcode search
      url += "&postalcode=" + si;
    } else if (si.length > 2) {
      // use address or airport codes lookup
      url += "&q=" + si;
    } else {
      return;
    }

    console.log(url);
    let geocode = fetch(url)
      .then(res => res.json())
      .then(
        (result) => {
          if (result.length > 0) {
            return {
              name: result[0].address.city || 
                    result[0].display_name.split(",")[0],
              lat: parseFloat(result[0].lat).toFixed(6),
              lng: parseFloat(result[0].lon).toFixed(6)
            }
          }
          return {};
        }
      )

    geocode.then(geo => {
      if (geo !== {}) {
        let url = NOAAPOINT_URL + geo.lat + "," + geo.lng;
        console.log(url);
        fetch(url)
          .then(res => res.json())
          .then(
            (result) => {
              let l = {
                name: geo.name,
                lat: parseFloat(geo.lat),
                lng: parseFloat(geo.lng),
                hfurl: result.properties.forecastHourly,
                dfurl: result.properties.forecast
              }
              let loc = this.state.locations;
              loc.push(l);
              this.setState(
                { locations: loc, searchInput: "" },
                localStorage.setItem("locations", JSON.stringify(loc))
              );
            }
          )
      }
    })
  }

  deleteLocation = (event) => {
    let loc = this.state.locations;
    if (event.target.value !== -1) {
      loc.splice(event.target.value, 1);
      this.setState(
        { locations: loc },
        localStorage.setItem("locations", JSON.stringify(loc))
      );
    }
  }

  showForecast = (index) => {
    this.props.onTabChange("forecast");
    let loc = this.state.locations[index];
    this.props.onLocChange(loc);
  }

  render() {
    return (
      <Container className="pl-1 pr-1">
        <Row className="mt-3 mb-2">
          <Col className="mt-2">
            <img className="today-logo" src="./logo.png" alt="weather-logo" />
            <strong>Weather</strong>
          </Col>
          <Col xs={3} sm={2}>
            <Button onClick={this.toggleEditMode}>+/-</Button>
          </Col>
        </Row>
        <hr />
        {this.state.locations.map((loc, index) => (
          <Row className="row-striped" 
            style={{ height: "3rem", lineHeight: "3rem", cursor: "pointer" }}>
              <Col xs={2} className={`${this.state.editMode ? "" : "d-none"}`}>
                <Button variant="danger" value={index}
                  onClick={this.deleteLocation}>
                X
                </Button>
              </Col>
              <Col onClick={() => this.showForecast(index)}>
                <strong>{loc.name}</strong>
              </Col>
          </Row>
        ))}
        <Row>
          <br/>
          <hr/>
          <InputGroup className={`mt-3 ${this.state.editMode ? "" : "d-none"}`}>
            <FormControl
              type="text"
              placeholder="ZIP Code or US Address"
              value={this.state.searchInput}
              onChange={this.setSearchInput}
              onKeyPress={event => {
                if (event.key === "Enter") {
                  this.addLocation();
                }
              }}
            />
            <Button onClick={this.addLocation}>+</Button>
          </InputGroup>
        </Row>
      </Container>
    )
  }
}

